body {
    background-color: #f7f8fa;
}
.ant-btn {
    border-radius: 6px;
    box-shadow: rgb(0 0 0 / 8%) 0px 6px 10px;
    
    width: 7rem;
}
.card {
    border-radius: 20px;
    border: 1px solid rgb(237, 238, 242);
    padding: 1rem;
    width: 100%;
}
.ant-radio-button-wrapper {
    font-size: 4px;
    width: 3.5rem;
    padding: 0 15px;
    border-radius: 16px;
    box-shadow: rgb(0 0 0 / 8%) 0px 2px 4px;
}
.ant-input-number {
    border-radius: 7px;
    width: 7rem;
    box-shadow: rgb(0 0 0 / 8%) 0px 2px 4px;
}
.create-btn {
  margin-top: 1rem;
  border-radius: 10px;
  height: 3rem;
  width: 100%;
  background-color: rgb(232, 0, 111);
  color: #fff;
  font-weight: 500;
  font-size: 16px;
}
.stop-btn {
  border-radius: 6px;
  height: 2rem;
  background-color: rgb(232, 0, 111);
  color: #fff;
  font-weight: 500;
}
